import React, { Component } from "react";
import { connect } from "react-redux";
import { reset } from "redux-form";
import Swal from "sweetalert2";
import {
  getBruto,
  getNetto,
  getPkg,
  getQty,
  getRp,
} from "../../actions/LogicAction";
import { getTotalNW } from "../../actions/returnPenjualanAction";
import { getListTransaksiPenjualan } from "../../actions/transaksiPenjualanAction";
import { getListPOPenjualan } from "../../actions/validasiPOAction";
import { gagal, warning } from "../../Alert";
import { AxiosMasterGet } from "../../Axios";
import GlobalTabel from "../../GlobalComponent/GlobalTabel";

class FormPOCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "no_preorder",
          text: "Nomor PO",
        },
        {
          dataField: "kode_lokasi",
          text: "Lokasi",
        },
        {
          dataField: "kode_sales",
          text: "Sales",
        },
        {
          dataField: "total_netto",
          text: "Netto (Gr)",
        },
        {
          dataField: "disc_persen",
          text: "Disc (%)",
        },
        {
          dataField: "total_netto_disc_ext",
          text: "Netto - Disc Ext",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() => this.pilihBarcode(row)}
                >
                  Pilih
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getListPOPenjualan());
  }

  async pilihBarcode(data) {
    localStorage.setItem("no_po", data.no_preorder);
    let sebelum = JSON.parse(localStorage.getItem("transaksiPenjualan")) || [];
    let hasil = data.detail_barang;
    hasil.forEach((list, index) => {
      AxiosMasterGet(`jenis/get/by-kode-jenis/${hasil[index].kode_jenis}`)
        .then((res) => {
          var simpan = {
            netto_disc: list.netto_disc,
            gross: list.gross, //berat + bungkus
            netto: list.netto, //murni
            qty: 1,
            k: list.k,
            ongkos_rp: list.ongkos_rp, //ongkos
            kadar_cetak: list.kadar_cetak,
            disc_persen: list.disc_persen,
            kadar_jual: list.kadar_jual, //harga%
            kadar_level2: res.data.level2,
            kadar_level3: res.data.level3,
            kadar_modal: list.kadar_modal, //harga%
            kode_jenis: list.kode_jenis, //kodebarang
            kode_barcode: "-",
            p: list.p,
            disc: list.disc,
            pkg: list.pkg,
            bruto: list.bruto, //berat
          };
          var simpan_level2 = {
            netto_disc: (parseFloat(list.bruto) * res.data.level2) / 100,
            gross: parseFloat(list.bruto) + parseFloat(list.pkg), //berat + bungkus
            netto: (parseFloat(list.bruto) * res.data.level2) / 100, //murni
            qty: 1,
            k: 0,
            ongkos_rp: list.ongkos_rp, //ongkos
            kadar_cetak: list.kadar_cetak,
            disc_persen: list.disc_persen,
            kadar_jual: list.kadar_jual, //harga%
            kadar_level2: res.data.level2,
            kadar_level3: res.data.level3,
            kadar_modal: list.kadar_modal, //harga%
            kode_jenis: list.kode_jenis, //kodebarang
            kode_barcode: "-",
            p: 0,
            disc: 0,
            pkg: list.pkg,
            bruto: list.bruto, //berat
          };
          var simpan_level3 = {
            netto_disc: (parseFloat(list.bruto) * res.data.level3) / 100,
            gross: parseFloat(list.bruto) + parseFloat(list.pkg), //berat + bungkus
            netto: (parseFloat(list.bruto) * res.data.level3) / 100, //murni
            qty: 1,
            k: 0,
            ongkos_rp: list.ongkos_rp, //ongkos
            kadar_cetak: list.kadar_cetak,
            disc_persen: 0,
            kadar_jual: list.kadar_jual, //harga%
            kadar_level2: res.data.level2,
            kadar_level3: res.data.level3,
            kadar_modal: hasil[index].kadar_beli, //harga%
            kode_jenis: list.kode_jenis, //kodebarang
            kode_barcode: "-",
            p: 0,
            disc: 0,
            pkg: list.pkg,
            bruto: list.bruto, //berat
          };
          var array =
            JSON.parse(localStorage.getItem("transaksiPenjualan")) || [];
          var array_level2 =
            JSON.parse(localStorage.getItem("transaksiPenjualan_level2")) || [];
          var array_level3 =
            JSON.parse(localStorage.getItem("transaksiPenjualan_level3")) || [];
          let posisi = sebelum.findIndex(
            (data) =>
              list.kode_jenis === data.kode_jenis || data.netto === list.netto
          );
          console.log(list.netto);
          console.log(posisi);
          // return false;
          if (posisi < 0) {
            array.push(simpan);
            array_level2.push(simpan_level2);
            array_level3.push(simpan_level3);
            var GrandTotalNetto = array
              .map((list) => parseFloat(list.netto_disc))
              .reduce((a, b) => a + b, 0);
            localStorage.setItem("GrandTotalNetto", GrandTotalNetto);
            localStorage.setItem("transaksiPenjualan", JSON.stringify(array));
            localStorage.setItem(
              "transaksiPenjualan_level2",
              JSON.stringify(array_level2)
            );
            localStorage.setItem(
              "transaksiPenjualan_level3",
              JSON.stringify(array_level3)
            );
            Swal.fire({
              position: "top-end",
              icon: "success",
              text: "Data Berhasil Ditambahkan!",
              showConfirmButton: false,
              timer: 1500,
            });
            this.props.dispatch(getListTransaksiPenjualan());
            this.props.dispatch(getTotalNW());
            this.props.dispatch(reset("FormTransaksiPenjualan"));

            this.props.dispatch(getNetto("transaksiPenjualan"));
            this.setState({
              discGlobalGr:
                (parseFloat(0) / 100) *
                parseFloat(localStorage.getItem("GrandTotalNetto")),
              discGlobalPr: 0,
              GrandTotalNetto: (
                parseFloat(localStorage.getItem("GrandTotalNetto")) - 0
              ).toFixed(3),
            });
          } else {
            warning("Data Sudah Ada Di Tabel");
          }
        })
        .catch((err) =>
          gagal("Kode Kelompok Belum Disesuaikan, Mohon Sesuaikan Dahulu")
        );
    });
  }
  getSumData() {
    this.props.dispatch(getNetto("ReturnPenjualan"));
    this.props.dispatch(getBruto("ReturnPenjualan"));
    this.props.dispatch(getRp("ReturnPenjualan"));
    this.props.dispatch(getPkg("ReturnPenjualan"));
    this.props.dispatch(getQty("ReturnPenjualan"));
  }

  render() {
    return (
      <div className="col-lg-12 col-md-12">
        <GlobalTabel
          keyField="kodeJenis"
          data={this.props.listPO}
          columns={this.state.columns}
          textEmpty="Data Timbangan Kosong"
        />
      </div>
    );
  }
}

export default connect((state) => {
  return {
    listPO: state.provinsi.listPO,
  };
})(FormPOCustomer);
